import React, {Component} from "react";
import s from "./App.scss"

class App extends Component {

  constructor(props) {
    super(props);

    this.handleIncrement = this.handleIncrement.bind(this);
  }

  static defaultProps = {
    count: 0
  };

  state = {
    count: 0
  };

  handleIncrement() {
    console.log("Holaaa");
    this.setState({
      count: this.state.count + 1
    })
  }

  render() {
    return (
      <div>
        <h1>Hola reactjs con ES6 2</h1>
        <h4>Count: {this.props.count}</h4>
        <button type="button" class="btn btn-danger" onclick={this.handleIncrement}>Sumar</button>
      </div>
    );
  }
}

export default App;


